"use client";

import React, { useState } from "react";
import { Switch } from "@/components/ui/switch";
import { Button } from "@/components/ui/button";
import { useLanguage } from "@/app/providers";

interface Settings {
  privacy: {
    dataCollection: boolean;
    shareAnonymousData: boolean;
    personalizedAds: boolean;
  };
  notifications: {
    push: boolean;
    email: boolean;
    messageAlerts: boolean;
    recommendations: boolean;
  };
  theme: "light" | "dark" | "system";
  language: string;
}

export default function SettingsPanel() {
  const { language, setLanguage } = useLanguage();
  const [settings, setSettings] = useState<Settings>({
    privacy: {
      dataCollection: true,
      shareAnonymousData: false,
      personalizedAds: true,
    },
    notifications: {
      push: true,
      email: true,
      messageAlerts: true,
      recommendations: true,
    },
    theme: "system",
    language: language,
  });
  const [isSaving, setIsSaving] = useState(false);

  const handleSettingChange = (
    category: keyof Settings,
    setting: string,
    value: boolean
  ) => {
    setSettings((prev) => ({
      ...prev,
      [category]: {
        ...(prev[category] as Record<string, boolean>),
        [setting]: value,
      },
    }));
  };

  const handleThemeChange = (theme: Settings["theme"]) => {
    setSettings((prev) => ({ ...prev, theme }));
  };

  const handleSave = async () => {
    setIsSaving(true);
    // Simulate API call to save settings
    await new Promise((resolve) => setTimeout(resolve, 1000));
    setIsSaving(false);
  };

  const handleExportData = () => {
    // Simulate data export
    const data = {
      settings,
      messages: [],
      chatHistory: [],
      profile: {},
    };
    const blob = new Blob([JSON.stringify(data, null, 2)], {
      type: "application/json",
    });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "nexo-data-export.json";
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleDeleteAccount = () => {
    if (
      window.confirm(
        "¿Estás seguro de que deseas eliminar tu cuenta? Esta acción no se puede deshacer."
      )
    ) {
      // Simulate account deletion
      alert("Cuenta eliminada correctamente");
    }
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <section className="space-y-6">
        <div>
          <h3 className="text-xl font-semibold mb-4">Privacidad</h3>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <label className="font-medium">Recolección de datos de uso</label>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Permite recopilar datos sobre cómo usas la aplicación
                </p>
              </div>
              <Switch
                checked={settings.privacy.dataCollection}
                onCheckedChange={(checked) =>
                  handleSettingChange("privacy", "dataCollection", checked)
                }
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <label className="font-medium">Compartir datos anónimos</label>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Ayuda a mejorar la app compartiendo datos anónimos
                </p>
              </div>
              <Switch
                checked={settings.privacy.shareAnonymousData}
                onCheckedChange={(checked) =>
                  handleSettingChange("privacy", "shareAnonymousData", checked)
                }
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <label className="font-medium">Anuncios personalizados</label>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Recibe anuncios relevantes basados en tus intereses
                </p>
              </div>
              <Switch
                checked={settings.privacy.personalizedAds}
                onCheckedChange={(checked) =>
                  handleSettingChange("privacy", "personalizedAds", checked)
                }
              />
            </div>
          </div>
        </div>

        <div>
          <h3 className="text-xl font-semibold mb-4">Notificaciones</h3>
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <label className="font-medium">Notificaciones push</label>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Recibe notificaciones en tiempo real
                </p>
              </div>
              <Switch
                checked={settings.notifications.push}
                onCheckedChange={(checked) =>
                  handleSettingChange("notifications", "push", checked)
                }
              />
            </div>
            <div className="flex items-center justify-between">
              <div>
                <label className="font-medium">Notificaciones por email</label>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  Recibe actualizaciones importantes por correo
                </p>
              </div>
              <Switch
                checked={settings.notifications.email}
                onCheckedChange={(checked) =>
                  handleSettingChange("notifications", "email", checked)
                }
              />
            </div>
          </div>
        </div>

        <div>
          <h3 className="text-xl font-semibold mb-4">Apariencia</h3>
          <div className="space-y-4">
            <div>
              <label className="font-medium block mb-2">Tema</label>
              <div className="grid grid-cols-3 gap-4">
                {(["light", "dark", "system"] as const).map((theme) => (
                  <button
                    key={theme}
                    onClick={() => handleThemeChange(theme)}
                    className={`p-3 rounded-lg border ${
                      settings.theme === theme
                        ? "border-blue-500 bg-blue-50 dark:bg-blue-900/20"
                        : "border-gray-200 dark:border-gray-700"
                    }`}
                  >
                    <span className="capitalize">{theme}</span>
                  </button>
                ))}
              </div>
            </div>
          </div>
        </div>

        <div>
          <h3 className="text-xl font-semibold mb-4">Idioma</h3>
          <select
            value={settings.language}
            onChange={(e) => {
              setSettings((prev) => ({ ...prev, language: e.target.value }));
              setLanguage(e.target.value as any);
            }}
            className="w-full p-2 rounded-lg border border-gray-300 dark:border-gray-600"
          >
            <option value="es">Español</option>
            <option value="en">English</option>
            <option value="fr">Français</option>
            <option value="de">Deutsch</option>
            <option value="zh">中文</option>
          </select>
        </div>

        <div>
          <h3 className="text-xl font-semibold mb-4">Gestión de datos</h3>
          <div className="space-y-4">
            <Button
              variant="outline"
              className="w-full justify-start"
              onClick={handleExportData}
            >
              Exportar mis datos
            </Button>
            <Button
              variant="destructive"
              className="w-full justify-start"
              onClick={handleDeleteAccount}
            >
              Eliminar mi cuenta
            </Button>
          </div>
        </div>
      </section>

      <div className="sticky bottom-0 bg-white dark:bg-gray-900 p-4 border-t border-gray-200 dark:border-gray-700">
        <Button
          className="w-full"
          onClick={handleSave}
          disabled={isSaving}
        >
          {isSaving ? "Guardando..." : "Guardar cambios"}
        </Button>
      </div>
    </div>
  );
}
